@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,400;0,500;1,400;1,500&display=swap);
:root{--color-font: #353D60;--color-background: #e0e0e0;--aside-width: 15rem}body{margin:0;color:#353D60;color:var(--color-font);font:normal 1rem / 1.25 'Rubik', 'Segoe UI', 'Roboto', Arial, sans-rerif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#e0e0e0;background-color:var(--color-background)}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{box-sizing:border-box;max-width:68rem;width:100%;margin:0 auto;padding:0 2rem}.App .header{display:flex;margin:0 auto 1.5rem;padding:2rem 1rem;justify-content:space-between;align-items:center}.App .header .logo{display:block;width:13rem;height:7.5rem;overflow:hidden;border-radius:1rem;background:var(--color-background);box-shadow:0.25rem 0.25rem 0.5rem #cccccc, -0.25rem -0.25rem 0.5rem #f4f4f4}.App .header .logo__link{display:block;height:100%;font-size:0;line-height:0;background:var(--color-background) url(/static/media/g-search-logos_transparent.5aafea02.png) 50% 50%/cover}.App .header .description{font:normal 1.5rem / 1.2 'Rubik', 'Segoe UI', 'Roboto', Arial, sans-rerif;text-shadow:0.125rem 0.125rem 0.25rem #cccccc, -0.125rem -0.125rem 0.25rem #f4f4f4}.App .main-content{display:flex}.App .main-content__results{box-sizing:border-box;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;width:calc(100% - var(--aside-width));padding:0 0 2rem 2rem}.App .main-content__aside{width:var(--aside-width);order:-1}.App .search-result{display:flex;justify-content:flex-start;align-items:stretch;flex-wrap:wrap;margin:0;padding:0}.App .search-result__item{box-sizing:border-box;width:calc(50% - 3rem);min-width:15rem;margin:1.5rem;padding:0.5rem;list-style:none;border-radius:1rem;background:var(--color-background);box-shadow:0.25rem 0.25rem 0.5rem #cccccc, -0.25rem -0.25rem 0.5rem #f4f4f4}.App .search-result__item-wrapper{height:calc(100% - 1rem);padding:0.5rem;border-radius:1.5rem;background:var(--color-background)}.App .search-result .title{margin:0 auto 0.5rem;word-break:break-all}.App .search-result .title__link{color:#353D60;text-decoration:none}.App .search-result .feature{margin:0 0 0.5rem;line-height:1.5}.App .search-result .feature--rowed{overflow:hidden;margin:0 0 0.25rem}.App .search-result .feature--rowed .feature__term{display:inline-block;margin:0 -1rem 0.25rem 0}.App .search-result .feature--rowed .feature__description{display:inline-block;margin:0 0 0.25rem}.App .search-result .feature__term{margin:0 0 0.25rem;font-weight:500}.App .search-result .feature__description{margin:0;text-indent:1.5rem;word-break:break-word}.App .search-result-paging{margin:0 0 2rem;white-space:nowrap}.App .search-result-paging__input{width:4rem;margin:0 1rem;padding:0.5rem;border:none;border-radius:0.5rem;background:var(--color-background);box-shadow:inset 0.5rem 0.5rem 0.25rem #cccccc, inset -0.5rem -0.5rem 0.25rem #f4f4f4}.App .search-result-paging__button{box-sizing:border-box;display:inline-block;width:2.25rem;height:2.25rem;margin:0 1rem;padding:0.125rem;font:normal 1.5rem / 1 'Rubik', 'Segoe UI', 'Roboto', Arial, sans-rerif;border:none;border-radius:1rem;background:var(--color-background);box-shadow:0.25rem 0.25rem 0.5rem #cccccc, -0.25rem -0.25rem 0.5rem #f4f4f4}.App .search-result-paging__button:hover{cursor:pointer}.App .search-result-paging__button:hover:disabled{cursor:default}.App .input--search{width:15rem;margin:0 0 1.5rem;padding:0.5rem;border:none;border-radius:0.5rem;background:var(--color-background);box-shadow:inset 0.5rem 0.5rem 0.25rem #cccccc, inset -0.5rem -0.5rem 0.25rem #f4f4f4}.App .search-history{margin:0 0 2rem}.App .search-history__title{margin:0 0 1.5rem;text-align:center}.App .search-history__list-order{font:normal 0.75rem / 1.95 'Rubik', 'Segoe UI', 'Roboto', Arial, sans-rerif}.App .search-history__list{margin:0 auto;padding:0}.App .search-history__list-item{box-sizing:border-box;width:15rem;margin:0 0 0.5rem;padding:0.25rem 0.5rem;list-style:none;border-radius:0.5rem;background:var(--color-background);box-shadow:0.25rem 0.25rem 0.5rem #cccccc, -0.25rem -0.25rem 0.5rem #f4f4f4}

